import styled from "styled-components";

export default styled.div `
width:100%;
background-color:rgb(255,255,255);
border-radius:.2rem;
margin-top:2rem;
box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
z-index:-1;
`;



