import styled from "styled-components";

export default styled.table `
min-width:700px;
width: 100%;
margin-top: 1rem;
display: table;
font-family: "Roboto", "Helvetica", "Arial", sans-serif;
border-spacing: 0;
border-collapse: collapse;

`;



